<template>
  <v-app light v-scroll="onScroll">

    <v-parallax style="height: 100vh" jumbotron src="/static/img/paisaje.jpg">
      <v-layout column align-center justify-center>
        <h2 class="white--text">J. Hermosilla</h2>
        <h4 class="white--text bold--text pa-2" style="background: rgba(0,0,0,0.5)">GIS & Web Mapping!</h4>
      </v-layout>
    </v-parallax>

    <v-card color="grey lighten-5 primary" flat>

      <v-layout row>
        <v-flex xs10 offset-xs1>
          <v-scale-transition>

            <v-card class="card--flex-toolbar">
              <v-toolbar card color="white" prominent>
                <v-toolbar-title class="body-2 grey--text">

                  <v-avatar
                    size="48px"
                    class="grey lighten-4"
                  >
                    <img src="/static/img/joseahr.jpg" alt="avatar">
                  </v-avatar>

                  <span>Jose Ángel Hermosilla Rodrigo</span>
                </v-toolbar-title>
                <v-spacer></v-spacer>
                
              </v-toolbar>

              <v-divider></v-divider>

              <v-card-text>
                <h5 class="primary--text">¡Bienvenido!</h5>
                <p>Me llamo Jose Ángel y soy Ingeniero en Geomática y Topografía.</p>
                <p>Gracias a mi formación soy experto en diversas materias como: Geodesia, SIG, IDEs, Web Mapping...</p>
                <p>Me apasiona la programación y aprendo cada día de las nuevas tecnologías.</p>

                <v-divider></v-divider>

                <v-layout row>
                  <v-flex xs12 class="text-xs-center">
                    <v-btn icon href="https://github.com/joseahr" target="_blank">
                      <i class="fa fa-github fa-2x"></i>
                    </v-btn>
                    <v-btn icon href="https://stackoverflow.com/users/3866134/jose-hermosilla-rodrigo?tab=profile" target="_blank">
                      <i class="fa fa-stack-overflow fa-2x"></i>
                    </v-btn>
                    <v-btn icon href="https://www.facebook.com/jose.hermosillarodrigo" target="_blank">
                      <i class="fa fa-facebook fa-2x"></i>
                    </v-btn>
                    <v-btn icon href="https://www.instagram.com/josehermosillarodrigo/" target="_blank">
                      <i class="fa fa-instagram fa-2x"></i>
                    </v-btn>
                    <v-btn icon href="https://www.linkedin.com/in/jose-%C3%A1ngel-hermosilla-rodrigo-70a590147/" target="_blank">
                      <i class="fa fa-linkedin fa-2x"></i>
                    </v-btn>
                  </v-flex>
                </v-layout>

                <v-divider></v-divider>

              </v-card-text>

              <v-card-actions>
                <v-layout row wrap text-xs-center>
                  <v-flex xs12 sm6 class="pa-2">
                    <v-btn block primary to="/profile">Saber Más</v-btn>
                  </v-flex>
                  <v-flex xs12 sm6 class="pa-2">
                    <v-btn block primary to="/portfolio">Trabajos</v-btn>
                  </v-flex>
                </v-layout>
              </v-card-actions>

            </v-card>

          </v-scale-transition>
        </v-flex>
      </v-layout>
      <v-card-actions class="pa-0" style="margin-top: 10px;">
        <v-layout row>
          <v-flex xs12>
            <v-footer>
              <div>© {{ new Date().getFullYear() }}</div>
            </v-footer>
          </v-flex>
        </v-layout>
      </v-card-actions>
    </v-card>
  </v-app>
</template>

<style>
.card--flex-toolbar {
  margin-top: -64px;
}

</style>

<script>
/* eslint-disable */

const log = console.log;
export default {
  data() {
    return {
      toolbarVisible: false,
      parallaxHeight: 500,
      items: [
        { title: "Portfolio" },
        { title: "Proyectos GH" },
        { title: "Acerca de mí" }
      ]
    };
  },
  methods: {
    onScroll() {
      const offsetTop =
        window.pageYOffset || document.documentElement.scrollTop;
      this.toolbarVisible = offsetTop > 0;

      log(offsetTop);
    }
  }
};
</script>




